import * as React from 'react'
import type { HeadFC, PageProps } from 'gatsby';
import { Button, Container, Form } from 'react-bootstrap';

import Navigation from '../components/navigation';
import Footer from '../components/footer';

import love2CraftBgImage from '../images/love2craft-story-bg.png';
import customOrderImage1 from '../images/custom-order-image-1.png';
import customOrderImage2 from '../images/custom-order-image-2.png';

import './custom-orders.css';

interface ContactMe {
  name: string;
  phone: string;
  message: string;
}

const CustomOrdersPage: React.FC<PageProps> = () => {
  const [formData, setFormData] = React.useState<ContactMe>({
    name: '',
    phone: '',
    message: ''
  });

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleEmail = () => {
    if (typeof window !== 'undefined') {
      window.location.href = `mailto:love2craft@gmail.com?subject=${encodeURIComponent('custom order from' + formData.name)}&body=${encodeURIComponent(formData.message + '\nName:' + formData.name + '\nPhone: ' + formData.phone)}`;
    }
  };

  return (
    <>
      <Navigation />
      <main>
        <div className='row'>
          <Container className='col-12 col-xxl-6'>
            <div className='d-flex justify-content-center p-4 custom-order-text'>
              <div className='align-self-center'>
                <h1>Custom Builds</h1>
                <p className='px-4 custom-order-text'>Looking for something unique?</p>
                <p className='px-4 custom-order-text'>I’d love to create a custom piece for you! :)</p>
                <p className='px-4 custom-order-text'>Send me a message with your ideas, and I’ll get back to you soon!</p>
                <br />
              </div>
              <div className='custom-order-img-parent align-self-center p-4'>
                <img className='custom-order-img1' src={customOrderImage1} ></img>
                <img className='custom-order-img2' src={customOrderImage2}></img>
              </div>
            </div>
            <div style={{ minHeight: '150px' }} />

          </Container>
          <Container className='col-12 col-xxl-6 flex-fill d-flex my-story-container' style={{
            backgroundImage: `url(${love2CraftBgImage})`,
            backgroundRepeat: 'no-repeat',
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            minHeight: '600px',
            paddingRight: '0px',
            paddingTop: '0px'
          }}>
            <div className='my-story flex-fill p-4'>
              <div className='row'>
                <div className='p-4 col-12 col-lg-4 col-xl-5 col-xxl-7'>
                  <Form>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                      <Form.Label>Name</Form.Label>
                      <Form.Control type="text"
                        placeholder="Enter Name"
                        name="name"
                        value={formData.name}
                        onChange={handleChange} />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput2">
                      <Form.Label>Phone</Form.Label>
                      <Form.Control type="text"
                        placeholder="(123) 123-4567"
                        name="phone"
                        value={formData.phone}
                        onChange={handleChange} />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                      <Form.Label>Message *</Form.Label>
                      <Form.Control as="textarea" rows={5}
                        name="message"
                        value={formData.message}
                        onChange={handleChange}
                      />
                    </Form.Group>
                    <Button variant='light' onClick={() => {
                      handleEmail();
                    }}>Send</Button>
                  </Form>
                </div>
              </div>
            </div>
          </Container>
        </div>
      </main>
      <Footer />
    </>
  )
}

export default CustomOrdersPage

export const Head: HeadFC = () => <title>Custom Orders</title>